
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 60%;
  border: 1px solid #333;
  margin: 10px auto;
}
#view {
  padding: 10px;
  height: 450px;
  overflow: auto;
  color: #000;
  background-color: rgba(7,27,27,0.1);
}
#view::-webkit-scrollbar {
  width: 0;
}


