
.search .loading {
  width: 100%;
  height: 100%;
  background-color: #353535;
  text-align: center;
}
.search .searchBar .searchVal {
  border: none;
  line-height: 27px;
  padding: 0 10px;
  color: #777;
  font-size: 16px;
  font-weight: bold;
  border-radius: 5px;
}
.search .searchBar .searchVal:focus {
  color: #000;
}
.search .result>ul .song {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.search .result>ul .song .no {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.search .result>ul .song .songName {
  -webkit-box-flex: 3;
      -ms-flex: 3;
          flex: 3;
}
.search .result>ul .song .songArtists {
  -webkit-box-flex: 3;
      -ms-flex: 3;
          flex: 3;
}
.search .result>ul .song .songAlbum {
  -webkit-box-flex: 3;
      -ms-flex: 3;
          flex: 3;
}
.search .result>ul .song .songAlbum>i {
  cursor: pointer;
}


